import { Component, OnInit,OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Video } from '../domain/video';
import { VideoService } from '../service/video.service';
import { Http } from '@angular/http';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers:[VideoService]
})
export class HomeComponent implements OnInit,OnDestroy {

  req:any;
  homeImageList:[Video] = [] as [Video];
  videoDefaultImage = 'assets/img/videos/cat.jpg';

  constructor(private _router: Router,private _http:Http,private _videoService:VideoService) { }

  ngOnInit() {
    this.req = this._videoService.list().subscribe(
      data =>{
        data.filter(item =>{
          if(item.featured){
            this.homeImageList.push(item);
          }
        })
      }
    )
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.req.unsubscribe();
  }

  preventNormal(event,image){
    if(!image.prevented){
      this._router.navigate(['/videos']);
    }
  }

}



