import { Component, OnInit,OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Http } from '@angular/http';
import { Video } from '../domain/video';
import { VideoService } from '../service/video.service';

@Component({
  selector: 'videoDetail',
  templateUrl: './videoDetail.component.html',
  styleUrls: ['./videoDetail.component.css'],
  providers:[VideoService]
})
export class VideoDetailComponent implements OnInit,OnDestroy {

  routeSub:any;
  req:any;
  video:Video;
  slug:string;

  constructor(private _router:ActivatedRoute,private _http:Http,private _videoService:VideoService) { }

  ngOnInit() {
    this.routeSub = this._router.params.subscribe(
    params => {
      this.slug = params['slug'];
      this.req = this._videoService.getSlug(this.slug).subscribe(
        data =>{
          this.video = data as Video;
        }
      )
    })
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.routeSub.unsubscribe();
    this.req.unsubscribe();
  }

  getEmbedURL(item){
    return 'https://youtube.com/embed/'+item.embed+'?ecver=2';
  }

}



