import { Component, OnInit,OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Video } from '../domain/video';
import { VideoService } from '../service/video.service'; 

@Component({
  selector: 'searchDetail',
  templateUrl: './searchDetail.component.html',
  styleUrls: ['./searchDetail.component.css'],
  providers:[VideoService]
})
export class SearchDetailComponent implements OnInit,OnDestroy {

  req:any;
  routeSub:any;
  videosList:[Video];
  query:string;

  constructor(private _activatedRoute:ActivatedRoute,private _videoService:VideoService) { }

  ngOnInit() {
    this.routeSub = this._activatedRoute.params.subscribe(
      params =>{
        console.log(params);
        this.query = params['q'];
        this.req = this._videoService.search(this.query).subscribe(
          data => {
            this.videosList = data as [Video];
          }
        )
      }
    )
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.routeSub.unsubscribe();
    this.req.unsubscribe();
  }

}



